import { defineComponent, defineAsyncComponent, h } from 'vue'
import { useSuspense } from 'composable/components'
import Card from 'bbUi/components/card/card.vue'
import Breadcrumb from './Breadcrumb.vue'

// const Items = defineAsyncComponent(() => import('views/buyback/local/Tab.vue'))
const Items = defineAsyncComponent(() => import('./Items.vue'))
const Show = defineAsyncComponent(() => import('./Show'))

export default defineComponent({
  components: {
    Items,
    Show
  },
  setup() {
    const { genSuspense } = useSuspense()

    return () => {
      return [
        h(Breadcrumb),
        h('div', {
          class: 'grid xl:grid-cols-4 grid-cols-1 gap-4'
        }, [
          h('div', {
            class: 'xl:col-span-3 xl:order-first order-last'
          }, h(Card, {
            class: 'mb-6'
          }, {
            default: () => genSuspense({
              component: Items,
              props: {
                customer: true
              }
            })
          })),
          h('div', {}, [
            genSuspense(Show)
          ])
        ])
      ]
    }
  }
})